@import url("https://fonts.googleapis.com/css2?family=Overpass:wght@300;600&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Ubuntu:wght@400;500;700&display=swap");
/* Box sizing rules */
*,
*::before,
*::after {
  box-sizing: border-box;
}

/* Remove default margin */
body,
h1,
h2,
h3,
h4,
p,
figure,
blockquote,
dl,
dd {
  margin: 0;
}

/* Remove list styles on ul, ol elements with a list role, which suggests default styling will be removed */
ul[role=list],
ol[role=list] {
  list-style: none;
}

ul {
  margin: 0;
  padding: 0;
}

/* Set core root defaults */
html:focus-within {
  scroll-behavior: smooth;
}

/* Set core body defaults */
body {
  min-height: 100vh;
  text-rendering: optimizeSpeed;
  line-height: 1.5;
}

/* A elements that don't have a class get default styles */
a:not([class]) {
  text-decoration-skip-ink: auto;
}

/* Make images easier to work with */
img,
picture {
  max-width: 100%;
  display: block;
}

/* Inherit fonts for inputs and buttons */
input,
button,
textarea,
select {
  font: inherit;
}

/* Remove all animations, transitions and smooth scroll for people that prefer not to see them */
@media (prefers-reduced-motion: reduce) {
  html:focus-within {
    scroll-behavior: auto;
  }

  *,
*::before,
*::after {
    animation-duration: 0.01ms !important;
    animation-iteration-count: 1 !important;
    transition-duration: 0.01ms !important;
    scroll-behavior: auto !important;
  }
}
:root {
  --clr-primary-red-100: #ff7a85;
  --clr-primary-red-200: #ff525d;
  --clr-primary-blue: #1f3f5b;
  --clr-neutral-white: white;
  --clr-neutral-blue-100: #c8c8cb;
  --clr-neutral-blue-300: #4b5862;
  --clr-neutral-blue-500: #25252d;
  --clr-background-intro-red-100: #ff8f70;
  --clr-background-intro-red-200: #ff3d54;
  --clr-background-body-blue-100: #3f4164;
  --clr-background-body-blue-200: #2c2d3f;
}

body {
  min-height: 100vh;
  color: var(--clr-neutral-blue-300);
}

.container {
  width: 88%;
  max-width: 1200px;
  margin: 0 auto;
}

h1 {
  font-family: "Overpass", sans-serif;
  font-weight: 700;
  text-align: center;
}

h4 {
  font-family: "Overpass", sans-serif;
  font-weight: 300;
  text-align: center;
}

li {
  list-style: none;
}

a {
  text-decoration: none;
}

button {
  margin: 1rem 0;
}

.btn {
  margin: 1rem 0;
  padding: 0.625rem 0.875rem;
  min-width: 8.4375rem;
  border-radius: 30px;
  border: none;
  font-family: "Ubuntu", sans-serif;
  font-weight: 700;
  text-align: center;
}

.btn-outlined {
  background: transparent;
  border: 2px solid var(--clr-neutral-white);
  color: var(--clr-neutral-white);
}
.btn-outlined:hover {
  background-color: var(--clr-neutral-white);
  color: var(--clr-primary-red-200);
}

.btn-normal {
  background-color: var(--clr-neutral-white);
  border: 2px solid var(--clr-neutral-white);
  color: var(--clr-primary-red-200);
}
.btn-normal:hover,
.btn-normal :focus {
  background-color: var(--clr-primary-red-100);
  color: var(--clr-neutral-white);
}

.btn-sign {
  background-image: linear-gradient(90deg, var(--clr-background-intro-red-100) 0%, var(--clr-background-intro-red-200) 100%);
  color: var(--clr-neutral-white);
}
.btn-sign:hover,
.btn-sign :focus {
  background-image: none;
  background-color: var(--clr-primary-red-100);
  color: var(--clr-neutral-white);
}
@media screen and (min-width: 43em) {
  .btn-sign {
    margin: 0;
    background-image: none;
    background-color: var(--clr-neutral-white);
    border: 2px solid var(--clr-neutral-white);
    color: var(--clr-primary-red-200);
  }
}

.header {
  padding: 1rem 0;
  background: url("../../../images/bg-pattern-intro.svg") top -280px left -220px no-repeat, linear-gradient(160deg, var(--clr-background-intro-red-100) 0%, var(--clr-background-intro-red-200) 35%);
  background-size: 260% 240%;
  color: var(--clr-neutral-white);
  border-radius: 0 0 0 6rem;
}
@media screen and (min-width: 43em) {
  .header {
    background: url("../../../images/bg-pattern-intro.svg") top -800px left -250px no-repeat, linear-gradient(160deg, var(--clr-background-intro-red-100) 0%, var(--clr-background-intro-red-200) 35%);
    background-size: 160% 410%;
  }
}
.header__menu {
  display: flex;
  align-items: center;
  justify-content: space-between;
  flex-wrap: wrap;
  padding: 3rem 0 1.5rem 0;
}
.header__menu #close {
  display: none;
}
@media screen and (min-width: 43em) {
  .header__menu {
    flex-wrap: nowrap;
  }
  .header__menu--hamburger {
    display: none;
  }
  .header__menu--logo {
    margin-right: 3rem;
  }
}
.header__nav {
  font-family: "Overpass", sans-serif;
  position: relative;
  width: 100%;
  margin-top: 1rem;
}
@media screen and (min-width: 43em) {
  .header__nav {
    margin-top: 0;
  }
}
.header__nav--container {
  width: 100%;
  display: none;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  padding: 1.2rem;
  border-radius: 5px;
  position: absolute;
  z-index: 10;
  background-color: var(--clr-neutral-white);
  box-shadow: 0 2.1px 2.7px rgba(0, 0, 0, 0.022), 0 5.2px 6.9px rgba(0, 0, 0, 0.031), 0 10.6px 14.2px rgba(0, 0, 0, 0.039), 0 21.9px 29.2px rgba(0, 0, 0, 0.048), 0 60px 80px rgba(0, 0, 0, 0.07);
}
@media screen and (min-width: 43em) {
  .header__nav--container {
    display: flex !important;
    position: inherit;
    width: auto;
    z-index: inherit;
    flex-direction: row;
    padding: 0;
    background-color: transparent;
    box-shadow: none;
  }
  .header__nav--container hr {
    display: none;
  }
}
.header__nav__element {
  width: 100%;
  cursor: pointer;
}
@media screen and (min-width: 43em) {
  .header__nav__element {
    width: auto;
  }
}
.header__nav__element--title {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 0.875rem 0 0.875rem 0;
}
@media screen and (min-width: 43em) {
  .header__nav__element--title {
    margin-right: 1rem;
  }
}
.header__nav__element--title h4 {
  margin-right: 8px;
  font-size: 1.2rem;
  font-weight: 600;
  color: var(--clr-primary-blue);
}
@media screen and (min-width: 43em) {
  .header__nav__element--title h4 {
    color: var(--clr-neutral-white);
  }
}
.header__nav__element--arrow-light {
  display: none;
  transition: all 200ms ease-in-out;
}
@media screen and (min-width: 43em) {
  .header__nav__element--arrow-light {
    display: block;
  }
}
@media screen and (min-width: 43em) {
  .header__nav__element--arrow-dark {
    display: none;
  }
}
.header__nav__element--links {
  display: none;
  flex-direction: column;
  transition: all 200ms ease-in-out;
  padding: 1rem 1rem;
  border-radius: 5px;
  background-color: var(--clr-neutral-blue-100);
  text-align: center;
  transform: scale(0.0001);
  transform-origin: top left;
  opacity: 0;
}
@media screen and (min-width: 43em) {
  .header__nav__element--links {
    position: absolute;
    min-width: 10em;
    padding: 1.5rem 1rem;
    background-color: var(--clr-neutral-white);
    text-align: left;
  }
}
.header__nav__element--links a {
  color: var(--clr-neutral-blue-300);
}
.header__nav__element--links a:hover,
.header__nav__element--links a :active,
.header__nav__element--links a :focus {
  font-weight: 600;
}
.header__nav__element .show {
  display: flex;
}
.header .login-container {
  width: auto;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
}
.header .login-container__log {
  margin-top: 1rem;
  color: var(--clr-primary-blue);
  font-weight: 600;
}
@media screen and (min-width: 43em) {
  .header .login-container__log {
    margin-top: 0;
    margin-right: 1rem;
    color: var(--clr-neutral-white);
  }
}
@media screen and (min-width: 43em) {
  .header .login-container {
    flex-direction: row;
    margin-left: auto;
  }
}
.header .hero {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.header .hero__title {
  margin-top: 3rem;
}
.header .hero__title h1 {
  font-size: 2.5rem;
  line-height: 1.3;
}
@media screen and (min-width: 43em) {
  .header .hero__title h1 {
    font-size: 3rem;
    line-height: 1.1;
  }
}
.header .hero__subtitle {
  margin-top: 0.625rem;
}
.header .hero__subtitle h4 {
  font-size: 1.3rem;
  line-height: 1.4;
  opacity: 0.8;
}
@media screen and (min-width: 43em) {
  .header .hero__subtitle h4 {
    font-size: 0.875rem;
  }
}
.header .hero__cta {
  width: 90%;
  margin: 2rem 0 6rem 0;
  display: flex;
  justify-content: space-around;
}
@media screen and (min-width: 43em) {
  .header .hero__cta {
    width: 30%;
  }
}

@keyframes scaleUp {
  0% {
    transform: scale(0.0001);
    opacity: 0;
  }
  100% {
    transform: scale(1);
    opacity: 1;
  }
}
.attribution {
  font-size: 11px;
  text-align: center;
}

.attribution a {
  color: #3e52a3;
}